
import Back from '@/components/back'
import Blur from 'vux/src/components/blur/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Range from 'vux/src/components/range/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import {cutAr} from '@/assets/js/filters'
import util from '@/assets/js/util'
import $ from 'jquery'
import _ from 'underscore'

export default {
directives: {
TransferDom
},
data () {
return {
index: 0,         // 当前播放歌曲下标
isLoading: true,  // 是否加载
isPlaying: false,  // 是否播放中
isOnHold: true,   // 是否按住歌词
song: {},         // 当前歌曲信息
mp3: {url: null}, // MP3来源
lyric: '暂无歌词', // 歌词
height: 0,        // 窗口高度
progress: 0,      // 播放进度
current: 0,       // 当前时间
duration: 0       // 总时间
}
},
computed: {
lyricIndex () {
if (this.lyric instanceof Array && this.lyric.length !== 0) {
for (let i in this.lyric) {
if (this.current - 1.8 < this.lyric[i][0]) {
return i
}
}
} else {
return -1
}
}
},
methods: {
preSong () {
if (this.index === 0) {
this.$vux.toast.text('已经是第一首啦~~', 'top')
return
}
this.index -= 1
this.songDetail()
},
nextSong () {
if (this.index === this.$store.state.songs.songs.length - 1) {
this.$vux.toast.text('已经是最后一首啦~', 'top')
return
}
this.index += 1
this.songDetail()
},
togglePlay () {
let $audio = this.$refs.audio
if ($audio.src.indexOf('null') !== -1) {
this.isPlaying = false
this.$vux.toast.text('sorry,暂未相关资源~', 'top')
return
}
if ($audio.paused) {
this.isPlaying = true
this.isOnHold = false
$audio.play()
this.updateProgress()
this.lyricRoll()
} else {
this.isPlaying = false
$audio.pause()
}
},
customTime (val) {
if (!this.isPlaying && this.mp3.url !== null) {
let $audio = this.$refs.audio
let current = val / 100 * $audio.duration
$audio.currentTime = current
let min = document.getElementsByClassName('range-min')[0]
min.innerHTML = util.formatTime(parseInt(current))
}
},
changeTime (time) {
let $audio = this.$refs.audio
$audio.currentTime = time
this.isOnHold = false
this.lyricRoll()
},
showPic (index) {
this.$refs.previewer.show(index)
},
songDetail () {
const vm = this
vm.song = vm.$store.state.songs.songs[vm.index]
vm.songLyric()
let id = vm.song.id
vm.isLoading = true
vm.axios.get(vm.api.music.detail, {params: {id}}).then((res) => {
vm.isLoading = false
if (res.data.status.code === 200) {
vm.mp3 = res.data.data.mp3
let $audio = vm.$refs.audio
$audio.src = vm.mp3.url
vm.togglePlay()
let timer = setInterval(() => {
if ($audio.duration) {
vm.duration = parseInt($audio.duration)
let max = document.getElementsByClassName('range-max')[0]
let min = document.getElementsByClassName('range-min')[0]
max.innerHTML = '00:00'
min.innerHTML = '00:00'
if (_.isUndefined(max)) {
clearInterval(timer)
return
}
max.innerHTML = util.formatTime(vm.duration)
clearInterval(timer)
vm.updateProgress()
}
}, 300)
}
}).catch(() => {
this.$vux.toast.text('请求接口失败~~', 'middle')
})
},
songLyric () {
let id = this.song.id
this.axios.get(this.api.music.lyric, {
params: {
id,
'lv': -1,
'kv': -1,
'tv': -1
}
}).then((res) => {
if (res.data.code === 200) {
if (res.data.hasOwnProperty('lrc')) {
this.lyric = util.lyricArr(res.data.lrc.lyric)
} else {
this.lyric = '暂无歌词'
}
}
}).catch(() => {
this.lyric = '暂无歌词'
})
},
lyricRoll () {
const vm = this
let $audio = this.$refs.audio
let timer = setInterval(() => {
let on = $('.lyric-all p.on')
if (vm.isOnHold || $audio.paused || on.length === 0) {
clearInterval(timer)
return
}
let top = on.parent().scrollTop() - (on.parent().offset().top - on.offset().top) - on.parent().height() / 2
on.parent().animate({
scrollTop: top
})
}, 3000)
},
updateProgress () {
const vm = this
let $audio = vm.$refs.audio
let min = document.getElementsByClassName('range-min')[0]
let timer = setInterval(() => {
if (_.isUndefined($audio) || $audio.ended) {
clearInterval(timer)
vm.nextSong()
} else if (_.isUndefined($audio) || $audio.paused) {
clearInterval(timer)
} else {
vm.current = parseInt($audio.currentTime)
min.innerHTML = util.formatTime(vm.current)
vm.progress = vm.current / vm.duration * 100
}
}, 300)
},
swiperChange (index) {
if (index && this.isOnHold) {
this.isOnHold = false
this.lyricRoll()
}
}
},
created () {
this.index = this.$route.params.index
this.songDetail()
},
mounted () {
const vm = this
vm.height = vm.$refs.play.offsetHeight
vm.$nextTick(() => {
let $progress = document.getElementsByClassName('progress')[0]
let $rangeHandle = $progress.getElementsByClassName('range-handle')[0]
let $ly = document.getElementsByClassName('lyric-all')[0]
$rangeHandle.addEventListener('touchstart', () => {
vm.togglePlay()
})
$rangeHandle.addEventListener('touchend', () => {
setTimeout(() => {
vm.togglePlay()
}, 300)
})
$ly.addEventListener('touchstart', () => {
vm.isOnHold = true
})
})
},
filters: {
cutAr
},
components: {
Range,
Blur,
Back,
Previewer,
Swiper,
SwiperItem,
InlineLoading
}
}



